@import "../group_variables";

@mixin mobile { // 768
	@media (max-width: $breakpoint-mobile) {
		@content;
	}
}
@mixin mobileMore { // 769
	@media (min-width: calc($breakpoint-mobile + 1px)) {
		@content;
	}
}

@mixin tablet { //  769 ~ 1024
	@media (min-width: calc($breakpoint-mobile + 1px)) and (max-width: $breakpoint-tablet) {
		@content;
	}
}

//tablet size 
@mixin tabletLess { // 1024
	@media (max-width: $breakpoint-tablet) {
		@content;
	}
}

//tablet size 
@mixin tabletMore { // 1025
	@media (min-width: calc($breakpoint-tablet + 1px)) {
		@content;
	}
}

// 
@mixin desktopSmall { // 1025 ~ 1279
	@media (min-width: calc($breakpoint-tablet + 1px)) and (max-width: calc($breakpoint-pc - 1px)) {
		@content;
	}
}

@mixin desktop { // 1280 
	@media (min-width: $breakpoint-pc) {
		@content;
	}
}